import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map, tap } from 'rxjs/operators';

import { Package } from './package.model';
import { of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PackageService {

  private cache: Array<Package> = null;

  constructor(
    private http: HttpClient,
  ) { }

  fetchAll() {
    if (this.cache) {
      return of(this.cache);
    }

    return this.http.get<Package[]>('assets/data/components.json')
      .pipe(
        tap(items => this.cache = items)
      );
  }

  fetchTags() {
    return this.fetchAll()
      .pipe(
        map(items => [...new Set(items.reduce(
          (result, item) => result.concat(item.tags),
          []
        ))])
      );
  }

  findByName(name: string) {
    return this.fetchAll().pipe(
      map(items => items.find(item => item.name === name))
    );
  }

  fetchReadme(pkg: Package) {
    return this.http.get<string>(
      `https://get-github-readme-v2.now.sh/${pkg.owner}/${pkg.repo}?branch=master`,
      {
        responseType: 'text' as 'json'
      }
    );
  }

}
