
import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { Package } from './package.model';
import { PackageService } from './package.service';
import { tap, switchMap } from 'rxjs/operators';

@Component({
  selector: 'app-package-list-container',
  template: `
    <ngx-loading [show]="loading" [config]="{ backdropBorderRadius: '3px' }"></ngx-loading>
    <div class="home-container">

      <div fxLayout="row"
            fxLayout.xs="column"
            fxFlexFill >

        <div fxFlex="50">
        <mdc-text-field label="Text" fullwidth
          [(ngModel)]="filterText"
          (ngModelChange)="onFilterChange()"></mdc-text-field>
        </div>
        <div fxFlex="50">
          <ng-select
            [items]="tags$ | async"
            [multiple]="true"
            [closeOnSelect]="false"
            [searchable]="true"
            placeholder="Select tags"
            [(ngModel)]="filterTags"
            (ngModelChange)="onFilterChange()">
            <ng-template ng-multi-label-tmp let-items="items" let-clear="clear">
              <div class="ng-value" *ngFor="let item of items | slice:0:2">
                <span class="ng-value-label">{{item}}</span>
                <span class="ng-value-icon right" (click)="clear(item)" aria-hidden="true">×</span>
              </div>
              <div class="ng-value" *ngIf="items.length > 2">
                <span class="ng-value-label">{{items.length - 2}} more...</span>
              </div>
            </ng-template>
          </ng-select>
        </div>

      </div>

      <a href="https://github.com/d3v0ps/angular-packages/issues" target="blank" style="text-decoration: none; color: #69511e !important;">
        <mdc-card class="demo-card" style="margin-bottom: 20px; background: #EAB543;" outlined>
          <mdc-ripple class="demo-card-article">
            Send me more packages
          </mdc-ripple>
        </mdc-card>
      </a>

      <app-package-list [pkgs]="filteredPkgs$ | async"
        (pkgClick)="onPkgClick($event)"
        (githubClick)="onGithubClick($event)"
        (homepageClick)="onHomepageClick($event)">
      </app-package-list>
    </div>
  `
})

export class PackageListContainerComponent implements OnInit {

  loading = false;

  pkgs$ = new BehaviorSubject<Package[]>([]);
  tags$ = new BehaviorSubject<string[]>([]);
  filteredPkgs$ = new BehaviorSubject<Package[]>([]);

  filterText = '';
  filterTags = [];

  constructor(
    private service: PackageService,
    private router: Router
  ) { }

  ngOnInit() {
    this.loading = true;
    this.service.fetchAll()
    .pipe(
      tap(result => this.pkgs$.next(result)),
      switchMap(() => this.service.fetchTags()),
      tap(tags => {
        this.tags$.next(tags);
        this.filterTags = tags;
        this.onFilterChange();
      })
    )
    .subscribe(() => this.loading = false);
  }

  onPkgClick(pkg: Package) {
    this.router.navigate(['home', pkg.owner, pkg.name]);
  }

  onGithubClick({ event, pkg}: { event: MouseEvent, pkg: Package }) {
    event.stopPropagation();
    window.open(`https://github.com/${pkg.owner}/${pkg.repo}`, '_blank');
  }

  onHomepageClick({ event, pkg}: { event: MouseEvent, pkg: Package }) {
    event.stopPropagation();
    window.open(pkg.url, '_blank');
  }

  onFilterChange() {
    this.filteredPkgs$.next(this.pkgs$.value.filter(
      pkg => {
        const matchesTags = pkg.tags.some(
          tag => this.filterTags.includes(tag)
        );

        const matchesText = pkg.name.toLowerCase().includes(this.filterText.toLowerCase()) ||
          pkg.description.toLowerCase().includes(this.filterText.toLowerCase());

        return matchesTags && matchesText;
      }
    ));
  }
}
